import React, { Component } from 'react'
import { Link, withRouter } from "react-router-dom";
import { gojsData } from "../../assets/data";
import "./index.less";

const nameMap = {
    [gojsData.baseHref]: gojsData.title,
};
for (let i = 0; i < gojsData.catalog.length; i++) {
    nameMap[gojsData.catalog[i].href] = gojsData.catalog[i].title
}
let check = (num, obj) => {
    for (let key in obj) {
        if (num >= obj[key][0] && num <= obj[key][1] && obj[key][0] !== obj[key][1]) return key + "——";
    }
}

class index extends Component {
    render() {
        const pathSnippets = this.props.location.pathname.split('/').filter(i => i);
        let length = pathSnippets.length, lastStr = Number(pathSnippets[length - 1]), href = "/" + pathSnippets[length - 2] + "/";
        let prevHref = href + (lastStr - 1);
        let nextHref = href + (lastStr + 1);
        const prev = nameMap[prevHref] ?
            <Link to={prevHref} className="pager">
                <div className="label">上一篇</div>
                <span className="title">{check(lastStr - 1, gojsData.section)}{nameMap[prevHref]}</span>
            </Link>
            : null
        const next = nameMap[nextHref] ?
            <Link to={nextHref} className="pager">
                <div className="label">下一篇</div>
                <span className="title">{check(lastStr + 1, gojsData.section)}{nameMap[nextHref]}</span>
            </Link>
            : null
        return (
            <div className="page-footer">
                <div className="content">
                    <div className="prev">{prev}</div>
                    <div className="next">{next}</div>
                </div>
            </div>
        )
    }
}

export default withRouter(index);